        </router-link>
      </div>

      &lt;!&ndash;<MenuItem name="1">
        看Ta说
      </MenuItem>
      <MenuItem name="2">
        看Ta说
      </MenuItem>
      <MenuItem name="3">
        看Ta说
      </MenuItem>&ndash;&gt;

      &lt;!&ndash;<router-link to="/dynamic">
        <MenuItem name="101">
          看Ta说
        </MenuItem>
      </router-link>&ndash;&gt;

      &lt;!&ndash;<Submenu name="99">
        <template slot="title">
          更多
        </template>
        <MenuGroup title="MORE">
          <MenuItem v-if="index > 6" v-for="(nav,index) in navigation" :name="index+1">
            <router-link :to="{path:'sortPage',query:{navId:nav.id}}">
            {{nav.title}}
            </router-link>
          </MenuItem>
        </MenuGroup>
      </Submenu>&ndash;&gt;

      <MenuItem name="99" class="clearBorderBottom search">
        <AutoComplete v-model="value3" :filter-method="filterMethod" @keyup.enter="search"
                      placeholder="搜索" style="width:200px">
