          <Icon type="search"></Icon>
          搜索
        </Button>

      </MenuItem>
    </Menu>
    -->
    <div class="nav-wrapper">
      <div class="nav-logo">
        <router-link to="/">
          <img @click="active(99)" class="logo" src="../../../src/assets/img/logo.jpg">
        </router-link>
      </div>
      <div class="nav-item">
        <router-link to="/">
          <div @click="active(99)" :class="{'clickAfter': 99 == navNum}" class="nav">
            推荐
          </div>
        </router-link>
      </div>
      <div v-for="(nav,index) in navigation" class="nav-item">
        <router-link :to="{path:'/sortPage',query:{navId:nav.id}}">
          <div @click="active(index)" :class="{'clickAfter': index == navNum}" class="nav">
            {{nav.title}}
          </div>
        </router-link>
      </div>
      <div class="nav-search">
        <AutoComplete v-model="value3" :filter-method="filterMethod" @keyup.enter="search"
                      placeholder="搜索" style="width:200px">
        </AutoComplete>

        <Button type="ghost" @click.stop="search">
          <Icon type="search"></Icon>
          搜索
        </Button>
      </div>

    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import {loadNav, loadNavCss, saveNavCss} from '../../common/js/store'

  export default{
    props: {
      logo: Object,
      PRIFIX: String,
      navigation: Array
    },
    data() {
      return {
        theme1: "light",
        value3: '',
        data3: ['Steve Jobs', 'Stephen Gary Wozniak', 'Jonathan Paul Ive'],
        navNum: 0
      }
    },
    watch: {
      '$route' () {
        this.navNum = loadNavCss()
      }
    },
    methods: {
      /*数据源*/
      hotSearch() {
        const array = []
        this.data3.forEach((search, i) => {
          i++
          search = i + " " + search
          array.push(search)
        })
        return array
      },
      /*过滤*/
      filterMethod (value, option) {
        return option.toUpperCase().indexOf(value.toUpperCase()) !== -1;
      },
      search() {
        /*window.open(this.PRIFIX + "/search?value=" + this.value3)*/
        /*window.open("http://localhost:8080/search?value=" + this.value3)*/
        if (this.value3 == '') {
          this.$Notice.error({
            title: "搜索不能为空哦~",
          })
        } else {
          this.$router.push('/search?search=' + this.value3)
          this.$emit('navSearch', this.value3)
          /*window.open("http://http://news.cdrysj.com:8888/search?search=" + this.value3)*/
        }
      },
      active(name) {
        if (name == 99) {
          saveNavCss(99)
          this.navNum = 99
        } else {
          saveNavCss(name)
          this.navNum = loadNavCss()
          name = this.navigation[name].id
          this.$emit('reload', name)
        }
      }
    },
    created() {
      this.navNum = loadNavCss()
    }
  }
</script>

<style lang="less" scoped>
  .navWrapper {
    position: fixed;
    font-size: 16px !important;
    top: 0;
    left: 0;
    background: #fff !important;
    width: 100%;
    height: 60px;
    -moz-box-shadow: 0px 0px 5px #999; /* 老的 Firefox */
    box-shadow: 0px 0px 5px #999;
    display: flex;
    justify-content: center;
  }


  .ivu-menu-item {
    font-size: 16px !important;
  }

  .logo {
    width: 158px;
    height: 45px;
  }

  .clearBorderBottom {
    border-bottom: none !important;
  }

  .nav-wrapper {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 999;
    min-width: 1350px;
    background-color: #ffffff;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 0 5px #999;
  }

  .nav-item {
    display: flex;
    align-items: center;
  }

  .nav-logo {
    width: 158px;
    height: 45px;
    margin-right: 20px;
  }

  .nav-logo img {
    width: 158px;
    height: 45px;
  }

  .nav {
    padding: 15px 25px;
    background-color: #fff;
    color: #444;
    font-size: 18px;
    border-bottom: 2px solid #fff;
  }

  .nav:hover {
    color: #ed4040;
    border-bottom: 2px solid #ed4040;
    cursor: pointer;
  }

  .clickAfter {
    color: #ed4040;
    border-bottom: 2px solid #ed4040;
    cursor: pointer;
  }

  .nav-search {
    margin-left: 10px;
  }

  /*.search {
    float: right !important;
    right: 108px;
  }*/

</style>



