<template>
  <Card style="width: 340px;margin-top: 10px;overflow: hidden" class="cc">
    <div class="radio-list">
      <h4 style="color: #000000" class="hi">
        电台
        <Icon type="music-note"></Icon>
      </h4>
      <p @click="showList">
        电台列表
        <Icon type="navicon"></Icon>
      </p>
    </div>

    <div v-if="list.length " style="overflow: hidden">
      <transition name="move">
        <div v-if="listFlag" class="radio-list-show">
          <ul>
            <li v-for="(i,index) in list" @click="playList(i,index)">
              <Icon type="android-arrow-dropright-circle" style="margin-right: 4px"></Icon>
              {{i.radioName}}
            </li>
          </ul>
        </div>
      </transition>
    </div>

    <div style="width: 100%;overflow: hidden;margin-top: 10px;">
      <div class="scroll-title">
        <div class="title">{{title}}</div>
        <div class="title2">{{title}}</div>
      </div>
    </div>
    <div class="radio">
      <Icon v-if="index > 0" type="skip-backward" class="mar" @click="back"></Icon>
      <Icon v-else type="skip-backward" class="marGray"></Icon>
      <Icon v-if="flag" type="play" class="mar" @click="play"></Icon>
      <Icon v-else type="pause" class="mar" @click="pause"></Icon>
      <Icon v-if="index < list.length - 1" type="skip-forward" class="mar" @click="next"></Icon>
      <Icon v-else type="skip-forward" class="marGray"></Icon>
    </div>

    <div id='pp'></div>
    <div style="overflow: hidden">
      <transition name="move">
        <Input v-if="messageFlag" v-model="message" type="textarea" :autosize="{minRows: 1,maxRows: 5}" placeholder="给主持人留个言吧..."></Input>
      </transition>
      <div v-if="!messageFlag" style="float: right;margin-bottom: 10px;margin-top: 10px;">
        <Button type="info" @click="showMessageFlag()">给Ta留言</Button>
      </div>
      <div v-if="messageFlag" style="float: right;margin-bottom: 10px;margin-top: 10px;">
        <Button type="ghost" @click="hideMessage">取消</Button>
        <Button type="info" @click="showMessage()">留言</Button>
      </div>
    </div>
  </Card>
</template>

<script type="text/ecmascript-6">
  import {saveURL} from '../../../../common/js/store'

  export default {
    props: {
      PRIFIX: String,
      userInfo: Object
    },
    data () {
      return {
        message: '',
        messageFlag: false,
        list: [],
        title: '',
        nowPlay: '',
        index: 0,
        flag: true,
        listFlag: false
      }
    },
    methods: {
      showMessageFlag () {
        this.messageFlag = true
      },
      showMessage () {
        if (!this.userInfo.flag) {
          this.$Message.error('请登录')
          let url = window.location.href + ''
          saveURL(url)
          setTimeout(function () {
            window.location.href = 'http://n.immnc.com/login'
          },500)
        } else if (this.message == '') {
          this.$Message.error('请输入内容')
        } else {
          let id = this.list[this.index].id
          var qs = require('qs')
          var self = this
          this.$http.post(this.PRIFIX + 'oa/radio/add/letter', qs.stringify({
            'letter': self.message,
            'userId': self.userInfo.id,
            'radioId': id
          })).then(function (response) {
            response = response.data
            if (response.code === 0) {
              self.$Message.success('留言成功')
              self.message = ''
              self.messageFlag = false
            }else if(response.code === 3) {
              self.$Message.error('请登录')
              let url = window.location.href + ''
              saveURL(url)
              setTimeout(function () {
                self.$router.push('/login')
              },500)
            }  else {
              self.$Message.error('留言失败')
            }
          }).catch(function (error) {})
        }
      },
      hideMessage () {
        this.message = ''
        this.messageFlag = false
      },
      showList () {
        this.listFlag = !this.listFlag
      },
      playList (item,index) {
        this.index = index
        this.flag = false
        this.title = item.radioName
        this.nowPlay = item.url
        var html = "<a href='" + this.nowPlay + "'style='display:block;width:17px;height:17px;position: absolute;' id=player> </a>"
        window.document.getElementById('pp').innerHTML = html
        setTimeout(function () {
          flowplayer("player", "http://n.immnc.com/static/radio/flowplayer-3.2.18.swf");
        }, 500)
      },
      play () {
        if (this.list.length > 0) {
          this.flag = false
          this.title = this.list[this.index].radioName
          this.nowPlay = this.list[this.index].url
          var html = "<a href='" + this.nowPlay + "'style='display:block;width:17px;height:17px;position: absolute;' id=player> </a>"
          window.document.getElementById('pp').innerHTML = html
          setTimeout(function () {
            flowplayer("player", "http://n.immnc.com/static/radio/flowplayer-3.2.18.swf");
          }, 500)
        }
      },
      pause () {
        if (this.list.length > 0) {
          this.flag = true
          var html = ""
          window.document.getElementById('pp').innerHTML = html
        }
      },
      back () {
        this.index = this.index - 1
        /*if (this.index <= 0) {
          this.index = this.list.length - 1
          console.log(this.index)
        }*/
        this.title = this.list[this.index].radioName
        this.nowPlay = this.list[this.index].url
        var html = "<a href='" + this.nowPlay + "'style='display:block;width:13px;height:13px;position: absolute;' id=player> </a>"
        window.document.getElementById('pp').innerHTML = html
        setTimeout(function () {
          flowplayer("player", "http://n.immnc.com/static/radio/flowplayer-3.2.18.swf");
        }, 500)

      },
      next () {
        this.index = this.index + 1
        /*if ((this.index + 1) >= this.list.length) {
          this.index = 0
        }*/
        this.title = this.list[this.index].radioName
        this.nowPlay = this.list[this.index].url
        var html = "<a href='" + this.nowPlay + "'style='display:block;width:14px;height:14px;position: absolute;' id=player> </a>"
        window.document.getElementById('pp').innerHTML = html
        setTimeout(function () {
          flowplayer("player", "http://n.immnc.com/static/radio/flowplayer-3.2.18.swf");
        }, 500)
      }
    },
    created () {
      var qs = require('qs')
      var self = this
      /*推送文章*/
      this.$http.post(this.PRIFIX + 'oa/radio/web/list', qs.stringify({
        'page': 1,
        'size': 10
      })).then(function (response) {
        response = response.data
        if (response.code === 0) {
          self.$Message.info('请确保Flash已打开')
          self.list = response.data.radio.content
          /*测试数据
          self.list = [
            {id:'5',radioName:'www',showState:'1',url:'http://wb-ali-pull.v.momocdn.com/momo/m_15349176679384f1a9d15951109371.flv?uniqtype=1&r=0cb6fff9916919340'},
            {id:'6',radioName:'qqq',showState:'1',url:'http://125.64.134.58/wb-hdl6.v.momocdn.com/mlive/m_153491698796636d7800ee54e305b108.flv?uniqtype=1&r=847f212aa0b5ae960&wshc_tag=0&wsts_tag=5b7d0686&wsid_tag=76703a22&wsiphost=ipdbm'}
          ]*/
          if (response.data.radio.content.length == 0) {
            self.title = '电台主持人正在赶来的路上'
          } else {
            self.nowPlay = self.list[0]
          }
        }
      }).catch(function (error) {
      })
    }
  }

</script>

<style lang="less" scoped>

  .radio-list-show {
    width: 100%;
    background-color: #222;
    color: #fff;
    padding: 4px 8px;
    border-radius: 10px;
    margin-top: 5px;
    overflow: hidden;
  }

  .radio-list-show ul li {
    padding: 6px;
    border-bottom: 1px solid #fff;
  }

  .radio-list-show ul li:hover{
    cursor: pointer;
  }

  .move-enter-active, .move-leave-active {
    transition: all .3s linear;
    transform: translateY(0);
  }

  .move-enter, .move-leave-to {
    transform: translateY(-100%);
  }

  .radio-list {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .radio-list p:hover {
    cursor: pointer;
  }

  #pp {
    position: absolute;
    opacity: 0;
    top: 0;
  }

  .radio {
    padding: 10px;
    display: flex;
    justify-content: space-around;
    align-items: center;
  }

  .mar {
    margin: 10px 5px;
    font-size: 18px;
  }

  .marGray {
    margin: 10px 5px;
    font-size: 18px;
    color: #cccccc;
  }

  .mar:hover {
    cursor: pointer;
  }

  .scroll-title {
    display: flex;
    align-items: center;
    width: 100%;
    white-space: nowrap;
  }

  .title {
    width: 100%;
    font-size: 18px;
    white-space: nowrap;
    -webkit-animation: marquee 10s linear infinite;
    animation: marquee 10s linear infinite;
  }

  .title2 {
    width: 100%;
    font-size: 18px;
    white-space: nowrap;
    -webkit-animation: marquee2 10s linear infinite;
    animation: marquee2 10s linear infinite;
  }

  @keyframes marquee {
    0% {
      transform: translateX(0);
    }
    100% {
      transform: translateX(-200%);
    }
  }

  @keyframes marquee2 {
    0% {
      transform: translateX(100%);
    }
    100% {
      transform: translateX(-100%);
    }
  }

</style>



